<template>
	<view class="container">
		<view class="list" v-if="defaultAddress||basicAddress.length>0">
			<view class="item" v-if="defaultAddress">
				<view class="content">
					<view class="top">
						<view class="text1 line">{{defaultAddress.province+defaultAddress.city+defaultAddress.county+defaultAddress.address}}</view>
						<view class="text2">默认</view>
					</view>
					<view class="bot">{{defaultAddress.name+' '+defaultAddress.mobile}}</view>
				</view>
				<image src="../../static/icon/edit.png" class="icon" @click="editAddress(defaultAddress)"></image>
			</view>
			<view class="item" v-for="(item,index) in basicAddress" v-if="basicAddress.length>0">
				<view class="content">
					<view class="top">
						<view class="text1 line">{{item.province+item.city+item.county+item.address}}</view>
					</view>
					<view class="bot">{{item.name+' '+item.mobile}}</view>
				</view>
				<image src="../../static/icon/edit.png" class="icon" @click="editAddress(item)"></image>
			</view>
		</view>
		<view  class="no-content" v-else>
			<view class="content">
				<image src="../../static/icon/no_address.png" class="icon"></image>
				<view class="text">您还没有添加收货地址哦~</view>
			</view>
		</view>
		<view class="add-btn-box">
			<button class="addBtn" @click="toCreateAddress">添加地址</button>
		</view>
	</view>
</template>

<script>
	import {mapState,mapGetters, mapActions} from 'vuex'
	export default{
		data(){
			return {
				
			}
		},
		onLoad(){
			
		},
		computed:{
			...mapState(['userInfo','addressList']),
			...mapGetters(['defaultAddress','basicAddress'])
		},
		methods:{
			...mapActions(['getAddressList']),
			editAddress(info){
				let  params =  encodeURIComponent(JSON.stringify(info))
				uni.navigateTo({
					url:`/pages/user/editAddress?info=${params}`
				})
			},
			toCreateAddress(){
				uni.navigateTo({
					url:'/pages/user/createAddress'
				})
			}
		},
		onShow(){
			this.getAddressList(this.userInfo.id)
		}
	}
</script>

<style lang="scss">
	.container{
		.list{
			padding-bottom: 135upx;
			.item{
				background-color: #fff;
				padding: 20upx 10upx 20upx 30upx;
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom:1px solid #F0F0F0 ;
				.content{
					.top{
						display: flex;
						align-items: center;
						justify-content: flex-start;
						.text1{
							max-width: 500upx;
							font-size: 28upx;
						}
						.text2{
							font-size: 20upx;
							color: #7CC58E;
							border: 1px solid #7CC58E;
							border-radius: 10upx;
							padding:0 10upx ;
							margin-left: 12upx;
						}
					}
					.bot{
						font-size: 24rpx;
						color: $uni-secondary-color;
						margin-top: 10upx;
					}
				}
				.icon{
					width:100upx;
					height: 100upx;
					padding: 25upx;
				}
			}
		}
		.add-btn-box{
			width: 100%;
			position: fixed;
			bottom: 0;
			left: 0;
			padding: 20upx;
			.addBtn{
				background-color: #009000;
				color: #fff;
			}
		}
	}
</style>